import React, { useState, useRef } from 'react'

import { Shader, Uniform, Attribute, Camera, Texture } from 'react-shader'

const glsl = x => x[0]

const vertex = glsl`
  precision highp float;

  attribute vec4 a_position;
  attribute vec4 a_color;
  attribute vec3 a_rotation;
  attribute vec3 a_speed;
  attribute float a_size;

  uniform float u_time;
  uniform mat4 u_projection;
  uniform vec3 u_worldSize;
  uniform float u_gravity;
  uniform float u_wind;

  varying vec4 v_color;
  varying float v_rotation;

  void main() {
    v_color = a_color;
    v_rotation = a_rotation.x + u_time * a_rotation.y;

    vec3 pos = a_position.xyz;

    pos.x = mod(pos.x + u_time + u_wind * a_speed.x, u_worldSize.x * 2.0) - u_worldSize.x;
    pos.y = mod(pos.y - u_time * a_speed.y * u_gravity, u_worldSize.y * 2.0) - u_worldSize.y;

    pos.x += sin(u_time * a_speed.z) * a_rotation.z;
    pos.z += cos(u_time * a_speed.z) * a_rotation.z;

    gl_Position = u_projection * vec4(pos.xyz, a_position.w);
    gl_PointSize = (a_size / gl_Position.w) * 100.0;
  }`

const fragment = glsl`
  precision highp float;

  uniform sampler2D u_texture;

  varying vec4 v_color;
  varying float v_rotation;

  void main() {
    vec2 rotated = vec2(
      cos(v_rotation) * (gl_PointCoord.x - 0.5) + sin(v_rotation) * (gl_PointCoord.y - 0.5) + 0.5,
      cos(v_rotation) * (gl_PointCoord.y - 0.5) - sin(v_rotation) * (gl_PointCoord.x - 0.5) + 0.5
    );

    vec4 snowflake = texture2D(u_texture, rotated);

    gl_FragColor = vec4(snowflake.rgb, snowflake.a * v_color.a);
  }`

const texture = 'data:image/png;base64,'

const Snow = () => {
  const [position, setPosition] = useState([])
  const [color, setColor] = useState([])
  const [rotation, setRotation] = useState([])
  const [speed, setSpeed] = useState([])
  const [size, setSize] = useState([])
  const [elapsed, setElapsed] = useState(0)
  const [worldSize, setWorldSize] = useState([0, 0, 0])
  const [wind, setWind] = useState(0)
  const windData = useRef({
    current: 0,
    force: 0.1,
    target: 0.1,
    min: 0.1,
    max: 0.25,
    easing: 0.005
  }).current

  const onUpdate = (delta, elapsed) => {
    setElapsed(elapsed)
    
    windData.force += (windData.target - windData.force) * windData.easing
    windData.current += windData.force * (delta * 0.2)
    setWind(windData.current)

    if (Math.random() > 0.995) {
      windData.target = (windData.min + Math.random() * (windData.max - windData.min)) * (Math.random() > 0.5 ? -1 : 1)
    }
  }

  const onResize = (w, h, dpi) => {
    const position = [], color = [], size = [], rotation = [], speed = []
    const height = 110
    const width = w / h * height
    const depth = 80

    Array.from({ length: w / h * 7000 }, () => {
      position.push(-width + Math.random() * width * 2, -height + Math.random() * height * 2, Math.random() * depth * 2)
      speed.push(1 + Math.random(), 1 + Math.random(), Math.random() * 10)
      rotation.push(Math.random() * 2 * Math.PI, Math.random() * 20, Math.random() * 10)
      color.push(1, 1, 1, 0.1 + Math.random() * 0.2)
      size.push(5 * Math.random() * 5 * (h * dpi / 1000))
    })

    setWorldSize([width, height, depth])
    setPosition(position)
    setColor(color)
    setRotation(rotation)
    setSize(size)
    setSpeed(speed)
  }

  const style = { position: 'absolute', width: '100%', height: '100%' }

  return (
    <Shader vertex={vertex} fragment={fragment} style={style} onResize={onResize} onUpdate={onUpdate}>
      <Camera perspective={true} />
      <Attribute name="a_position" size={3} value={position} main />
      <Attribute name="a_color" size={4} value={color} />
      <Attribute name="a_rotation" size={3} value={rotation} />
      <Attribute name="a_speed" size={3} value={speed} />
      <Attribute name="a_size" size={1} value={size} />
      <Uniform name="u_time" type="float" value={elapsed} />
      <Uniform name="u_worldSize" type="vec3" value={worldSize} />
      <Uniform name="u_gravity" type="float" value={100} />
      <Uniform name="u_wind" type="float" value={wind} />
      <Texture src={texture} />
    </Shader>
  )
}

export default Snow


